import clibrary from './videoinput.clibrary.js';

/**
 * @typedef {import('./typedef.js').WorkerCommand} WorkerCommand
 * @typedef {import('./typedef.js').DoProcessRequest} DoProcessRequest
 * @typedef {import('./typedef.js').DoConvertRequest} DoConvertRequest
 * @typedef {import('./typedef.js').SetMaskRequest} SetMaskRequest
 */

/** C++ */
let cLibrary;
/** C++ */
let cObject;

/**
 * @param {MessageEvent} e
 */
export async function onmessage(e) {
  /** @type {WorkerCommand} */
  const wc1 = e.data;
  /** @type {WorkerCommand} */
  const wc2 = {
    command: wc1.command,
    serial: wc1.serial,
  };
  const buffers = [];

  // C++
  if (!cObject) {
    if (!cLibrary) {
      cLibrary = await clibrary();
    }

    cObject = new cLibrary.MyObject();
  }

  if (wc1.command == 'doConvert') {
    /** @type {DoConvertRequest} */
    const req = wc1.payload;
    wc2.payload = cObject.doConvert(req.base64, req.ext);
  } else if (wc1.command == 'doFilter') {
    wc2.payload = cObject.doFilter(wc1.payload);
  } else if (wc1.command == 'doGrab') {
    wc2.payload = cObject.doGrab(wc1.payload);
  } else if (wc1.command == 'doProcess') {
    /** @type {DoProcessRequest} */
    const req = wc1.payload;
    // 
    const w1 = req.width;
    const h1 = req.height;
    const d1 = new Uint8ClampedArray(req.buffer);
    cObject.inputSize = { width: w1, height: h1 };
    cObject.inputData.set(d1);

    // 
    for (const key in req.settings) {
      if (Object.prototype.hasOwnProperty.call(req.settings, key)) {
        cObject[key] = req.settings[key];
      }
    }

    // 
    cObject.doProcess();
  } else if (wc1.command == 'setMask') {
    /** @type {SetMaskRequest} */
    const req = wc1.payload;
    cObject.setMask(req);
  }

  postMessage(wc2, buffers);
}
